/* em/i/cite */
em,i,cite{ font-style:normal
}
i{ display:inline-block
}
em{ font-family:Arial, Helvetica, sans-serif;
}
a{color:#444;text-decoration:none;outline:none;cursor:pointer;-webkit-user-select: none; -moz-user-focus: none; -moz-user-select: none;
}
a,a:hover,a:active,a:visited,a:link,a:focus{
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
    outline:none;
    background: none;
    text-decoration: none;
}
/* show/hide */
.hide{ display:none
}
.show{ display:block
}
/* input */
select{vertical-align:middle;font-family:"Microsoft YaHei";font-size:.386473rem; border:0;
}
input[type='text'],input[type='password']{color: #444; padding: 0; margin: 0; vertical-align:middle;font-family:"Microsoft YaHei";font-size:.386473rem; border:0; -webkit-appearance: none; -webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-user-modify: read-write-plaintext-only;
}
input:focus {outline: none;
}
input::-webkit-input-placeholder,textarea::-webkit-input-placeholder{color:#ccc; font-size:.338164rem
}
